import { useEffect, useState } from 'react';
import { useNavigate, useParams, Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { Icon } from '@iconify/react';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';
import { API_CHAT, API_TICKET, HOST_API } from '../../config';

import axios from '../../utils/axios';
import { fNumber } from '../../utils/formatNumber';
import { fDescriptionTypeDate } from '../../utils/formatTime';

import { setAvailableCart } from '../../redux/slices/cart';
import { dispatch, useSelector } from '../../redux/store';

import BuyTicketBanner from '../../sections/ticket/BuyTicketBanner';

import Page from '../../components/Page';
import CountChooser from '../../components/form/CountChooser';
import PageBanner from '../../components/wrappers/PageBanner';
import EventBannerSkeleton from '../../components/skeletons/EventBannerSkeleton';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import Accordion from '../../components/Accordion';

export default function TicketDetail() {
    const { id } = useParams();
    const { user } = useAuth();
    const { cart } = useSelector((store) => store.cart);
    const navigate = useNavigate();
    const [event, setEvent] = useState();
    const { t } = useLocales();
    const [ticket, setTicket] = useState();
    const { isAuthenticated } = useAuth();
    const [uploader, setUploader] = useState(null);
    const [loading, setLoading] = useState(false);
    const [processing, setProcessing] = useState(false);
    const [eventType, setEventType] = useState(null);
    const [count, setCount] = useState(0);
    const [myRequest, setMyRequest] = useState();

    useEffect(() => {
        setLoading(true);
        axios
            .get(`${API_TICKET.getTicketDetail}/${id}`)
            .then((res) => {
                const _ticket = res.data.data.ticket;
                if (_ticket) {
                    setEvent(_ticket?.event);
                    const _type = _ticket?.event?.types.filter((t) => t.typeId === _ticket?.eventTypeId)[0];
                    setEventType(_type);
                }

                const _uploader = res.data.data.uploader;
                setTicket(_ticket);
                setUploader(_uploader);

                // load chat content

                axios.get(`${API_CHAT.getPriceRequest}/${id}`).then((res) => {
                    if (res.status === 200) {
                        setMyRequest(res.data.data);
                    } else {
                    }
                });
            })
            .finally(() => {
                setLoading(false);
            });
    }, [id]);

    useEffect(() => {
        if (!isAuthenticated) {
            document.querySelector('#auth-modal-check').click();
        }
    }, [isAuthenticated]);

    const onChangeCounter = (amount) => {
        setCount(amount);
    };

    const onBuyTicket = () => {
        ticket.uploader = uploader;
        setProcessing(true);
        axios
            .put(API_TICKET.cart, { ticketId: id })
            .then(({ status }) => {
                if (status === 200) {
                    dispatch(setAvailableCart([...cart, ticket]));
                    navigate(`/cart`);
                }
                setProcessing(false);
            })
            .finally(() => {
                setProcessing(false);
            });
    };

    const addedCart = cart?.find((e) => e._id === id);
    return (
        <Page title="Buy Ticket">
            <PageBanner>
                <div className="container p-4 mb-8">
                    <div className="flex w-full flex-col gap-8">
                        {loading && <EventBannerSkeleton />}
                        {!loading && event && <BuyTicketBanner event={event} location={ticket.location} />}
                        {ticket && event && (
                            <div className="w-full flex flex-col gap-8">
                                <div className="flex w-full gap-4 items-center">
                                    <button
                                        onClick={() => navigate(-1)}
                                        className="rounded-lg border-primary/30 p-2 btn-sm text-primary hover:bg-primary/50 btn btn-primary bg-primary/30 w-[48px] h-[48px]"
                                    >
                                        <Icon icon="la:undo" className='text-[20px]' />
                                    </button>
                                    <div className="flex flex-col gap-1">
                                        <h5 className="text-[28px] leading-9">
                                            {ticket?.count} Ingresso - {eventType?.name}
                                        </h5>
                                        <h6 className="text-stone-400 text-[14px] leading-[18px]">
                                            {fDescriptionTypeDate(ticket?.dateTime)} <br />
                                        </h6>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                    <div className="mt-16 mb-16">
                        <div className="flex flex-col md:flex-row gap-4">
                            <div className="flex flex-col gap-8 h-fit w-full" >
                                <GradientBorderWrapper>
                                    <div className="w-full flex p-6 flex-col gap-8">
                                        <div className="flex flex-col gap-8">
                                            <div className="flex justify-between items-center">
                                                <CountChooser
                                                    max={ticket?.count}
                                                    current={1}
                                                    onChanged={onChangeCounter}
                                                    size="sm"
                                                    disabled={addedCart || ticket?.status === 'sold' || user?._id === ticket?.uploader}
                                                />
                                                <div className="flex gap-2 text-[28px] items-center">
                                                    <h5>R$ {fNumber(ticket?.sellPrice)} </h5>
                                                    <Icon
                                                        icon="mdi:information-variant-circle"
                                                        className="text-primary w-6 h-6"
                                                    />
                                                </div>
                                            </div>

                                            <div className="flex flex-col gap-4 w-full items-center">
                                                {/* {ticket?.changablePrice && (!myRequest || myRequest === null) && (
                                        <button
                                            className={`btn btn-primary px-8 py-4 h-auto text-[19px] rounded-2xl capitalize ${processing ? 'loading' : ''
                                                }`}
                                            onClick={onDiscussPrice}
                                        >
                                            {t('action.discuss_price')}
                                        </button>
                                    )}
                                    {ticket?.changablePrice && myRequest && (
                                        <button
                                            className={`btn btn-primary px-8 py-4 h-auto text-[19px] rounded-2xl capitalize ${processing ? 'loading' : ''
                                                }`}
                                            onClick={goConversation}
                                        >
                                            {t('action.go_conversation')}
                                        </button>
                                    )} */}
                                                <button
                                                    className={`btn btn-primary px-8 py-4 h-auto text-[19px] rounded-2xl capitalize w-full disabled:bg-secondary disabled:text-black font-normal ${processing ? 'loading' : ''
                                                        }`}
                                                    disabled={count !== ticket?.count || addedCart}
                                                    onClick={onBuyTicket}
                                                >
                                                    {t('action.buy_ticket')}
                                                </button>
                                                <p className="text-base text-stone-400" >
                                                    {t("description.origin_price", { price: ticket?.originPrice })}
                                                </p>
                                            </div>
                                        </div>
                                        {/* avatar */}
                                        <div className="flex w-full">
                                            <GradientBorderWrapper>
                                                <div className="p-4 flex gap-4 items-center">
                                                    <div className="avatar">
                                                        <div className="rounded-full h-[50px] w-[50px]">
                                                            <LazyLoadImage src={`${HOST_API}${ticket?.avatar}`} alt="" />
                                                        </div>
                                                    </div>
                                                    <div className="flex flex-col gap-1">
                                                        <h5 className="text-[19px]">{t("label.additional_information", { name: user?.fullName })}</h5>
                                                        {ticket?.requireAccount && (
                                                            <h6 className="text-primary">
                                                                * {ticket?.requireAccountDetail} <br />
                                                            </h6>
                                                        )}
                                                        <h6 className="text-stone-400 text-[14px]">
                                                            {/* {ticket?.seat?.join(' , ')} */}
                                                            Ingresso LOTE 4
                                                        </h6>
                                                        {ticket?.buyAccount && (
                                                            <h6 className="text-stone-400">Buyer Account: {ticket?.buyAccount}</h6>
                                                        )}
                                                    </div>
                                                </div>
                                            </GradientBorderWrapper>
                                        </div>
                                        {/* show message */}

                                    </div>
                                </GradientBorderWrapper>
                                <GradientBorderWrapper>
                                    <div className="w-full flex p-6 flex-col gap-8">
                                        <div className="text-[28px]">{t("title.common_questions")}</div>
                                        <div className="flex flex-col gap-4">
                                            <Accordion title="É seguro comprar ingressos com a FanTicket?" detail={true} >
                                                <div className="flex flex-col p-2 text-sm text-stone-400">
                                                    Yes, Fanticket does everything to ensure a safe transaction. To keep it safe we check uploaded
                                                    e-tickets and sellers in multiple ways.
                                                </div>
                                            </Accordion>
                                            <Accordion title="Quando vou receber os ingressos?" detail={true}>
                                                <div className="flex flex-col p-2 text-sm text-stone-400">
                                                    Yes, Fanticket does everything to ensure a safe transaction. To keep it safe we check uploaded
                                                    e-tickets and sellers in multiple ways.
                                                </div>
                                            </Accordion>
                                            <Accordion title="Posso entrar em contato com o vendedor?" detail={true}>
                                                <div className="flex flex-col p-2 text-sm text-stone-400">
                                                    Yes, Fanticket does everything to ensure a safe transaction. To keep it safe we check uploaded
                                                    e-tickets and sellers in multiple ways.
                                                </div>
                                            </Accordion>
                                            <Accordion title="Ainda tem dúvida?" detail={true} hideLine={true}>
                                                <div className="flex flex-col p-2 text-sm text-stone-400">
                                                    Yes, Fanticket does everything to ensure a safe transaction. To keep it safe we check uploaded
                                                    e-tickets and sellers in multiple ways.
                                                </div>
                                            </Accordion>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                            </div>

                            {/* user info */}
                            <div className="flex flex-col gap-8 flex-1">
                                <GradientBorderWrapper isMessage className="event-active-bg" >
                                    <div className="w-full flex p-4 gap-2">
                                        <div className="text-primary">
                                            <Icon icon="mdi:information-variant-circle" className='w-6 h-6' />
                                        </div>
                                        <div className="flex flex-col gap-1" >
                                            <h5 className="text-[19px] font-medium">{t("description.main_ad")}</h5>
                                            <Link to={`/ticket/manage/${ticket?._id}`} className="text-[19px] text-primary underline" >{t("title.manage_ad")}</Link>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                                <GradientBorderWrapper isMessage className="event-active-bg" >
                                    <div className="w-full flex p-4 gap-2">
                                        <div className="text-primary">
                                            <Icon icon="mdi:information-variant-circle" className='w-6 h-6' />
                                        </div>
                                        <div className="flex flex-col gap-1" >
                                            <h5 className="text-[19px] font-medium">{t("description.necessary_login_account")}</h5>
                                            <p className="text-sm" >{t("description.necessary_login_account_desc")}</p>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                                <GradientBorderWrapper>
                                    <div className="w-full flex gap-2 p-4">
                                        <div className="avatar">
                                            <div className="rounded-full h-[50px] w-[50px]">
                                                <LazyLoadImage src={`${HOST_API}${uploader?.avatar}`} alt="" />
                                            </div>
                                        </div>
                                        <div className="flex flex-col gap-1 min-w-[300px]">
                                            <h5 className="text-[19px] capitalize">{uploader?.fullName}</h5>

                                            <div className="flex gap-1 text-[14px] items-center">
                                                <Icon
                                                    icon={
                                                        uploader?.phoneVerification
                                                            ? `material-symbols:verified-user-rounded`
                                                            : `ic:baseline-not-interested`
                                                    }
                                                    className={`w-4 h-4 ${uploader?.phoneVerification ? 'text-success' : 'text-error'}`}
                                                />
                                                {t("label.verified_phone")}
                                            </div>

                                            <div className="flex gap-1 text-[14px] items-center">
                                                <Icon
                                                    icon={`material-symbols:verified-user-rounded`}
                                                    className={`w-4 h-4 text-success`}
                                                />
                                                {t("label.ticket_sold", { count: 1 })}
                                            </div>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                                <GradientBorderWrapper>
                                    <div className="w-full flex gap-2 py-4 px-6 justify-center min-w-[300px]">
                                        <div className="flex flex-col">
                                            <h5 className="text-[19px] mb-4">FanSecurity</h5>
                                            <div className='flex gap-[57px]' >
                                                <h6 className="text-stone-400 text-[14px]" >
                                                    {t("description.exchange_ticket")}
                                                </h6>

                                                <div className="flex items-center">
                                                    <div className="bg-success/50 flex gap-1 items-center rounded-full w-[70px] h-[70px] justify-center">
                                                        <Icon icon="ic:outline-verified-user" width={32} className='text-success/90' />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </GradientBorderWrapper>
                            </div>
                        </div>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
